
<template>
  <div class="textarea">
    <div class="question">{{ title }}</div>
    <div class="description">{{ description }}</div>
    <Input @on-blur="blur" @on-change="change" v-model="currentValue" type="textarea" :readonly="readonly" :rows="rows" :placeholder="placeholder"></Input>
  </div>
</template>

<script>
export default {
  name: 'textarea',
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    rows: {
      type: Number
    },
    value: {
      type: String,
      default: ''
    },
    additionalKeys: {
      type: Array
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-000000000017',
      currentValue: ''
    }
  },
  mounted () {
    this.currentValue = this.value
    this.change(this.value, true)
  },
  methods: {
    change (val, first) {
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: val,
        server: this.server,
        first
      })
    },
    blur () {
      if (this.additionalKeys && this.additionalKeys.length) {
        this.$emit('additional', {
          allowClose: true,
          additionalKeys: this.additionalKeys
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.textarea
  padding 0.1rem
  margin auto
  display flex
  flex-direction column
  font-size 1rem
  .question
    margin 0.1rem 0
</style>
